<template>
  <div
    class="w-full h-screen bg-gris-30 flex flex-col items-center justify-start"
  >
    <div class="w-full p-4 font-bold text-xl">
      <h1>Gestión de Sábanas</h1>
    </div>

    <div
      class="
        w-full
        h-full
        p-4
        flex flex-row
        items-start
        justify-center
        overflow-y-hidden
      "
    >
      <!-- COLUMNA 1 -->
      <div
        class="w-1/3 flex flex-col items-center justify-start h-full px-2 mr-4"
      >
        <div class="py-2 flex flex-col items-center justify-start w-full">
          <div
            class="relative inline-block text-gray-700 w-72 py-2"
            v-if="titulos.sabana.length"
          >
            <select
              class="
                w-full
                h-10
                pl-3
                pr-6
                text-base
                placeholder-gray-600
                border
                rounded-lg
                appearance-none
                focus:shadow-outline
              "
              placeholder="Regular input"
              v-model="ids.idHotel"
            >
              <option value="0">Seleccione el Hotel</option>
              <option
                v-for="(item, index) in hoteles"
                :key="index"
                :value="item.idHotel"
              >
                {{ item.hotel + " " + item.marca }}
              </option>
            </select>
            <div
              class="
                absolute
                inset-y-0
                right-0
                flex
                items-center
                px-2
                pointer-events-none
              "
            >
              <svg class="w-4 h-4 fill-current" viewBox="0 0 20 20">
                <path
                  d="M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z"
                  clip-rule="evenodd"
                  fill-rule="evenodd"
                ></path>
              </svg>
            </div>
          </div>

          <div class="rounded-lg shadow">
            <input
              type="text"
              name=""
              class="bg-white py-3 px-3 rounded-l-lg focus:outline-none"
              placeholder="Nombre de la sábana"
              v-model="titulos.sabana"
            />
            <button
              class="
                bg-blue-300
                text-blue-500
                p-3
                rounded-r-lg
                focus:outline-none
              "
              @click="crearSabana"
            >
              Crear
            </button>
          </div>
        </div>

        <div
          class="
            py-2
            flex flex-col
            w-full
            items-center
            justify-start
            max-h-90
            overflow-y-auto
          "
        >
          <div
            v-for="(item, index) in arraySabanas"
            :key="index"
            class="sabana flex flex-row"
            @click="
              ids.idSabana = item.idSabana;
              ids.idApartado = '';
              ids.idActividad = '';
              actualizarArrays();
            "
          >
            <span v-if="item.select" :class="[item.select ? 'mr-1' : '']">
              👉
            </span>
            <h1>{{ item.sabana }}</h1>
          </div>
        </div>
      </div>

      <!-- COLUMNA 2 -->
      <div class="w-1/3 flex flex-col items-center justify-start h-full px-2">
        <div class="py-2 flex flex-row items-center justify-start w-full">
          <div class="rounded-lg shadow">
            <input
              type="text"
              name=""
              class="bg-white py-3 px-3 rounded-l-lg focus:outline-none"
              placeholder="Añadir apartado"
              v-model="titulos.apartado"
            />
            <button
              class="
                bg-blue-300
                text-blue-500
                p-3
                rounded-r-lg
                focus:outline-none
              "
              @click="crearApartado"
            >
              Añadir
            </button>
          </div>
        </div>
        <div
          class="
            py-2
            flex flex-col
            w-full
            items-center
            justify-start
            max-h-90
            overflow-y-auto
          "
        >
          <div
            v-for="(item, index) in arrayApartados"
            :key="index"
            class="actividad flex items-center justify-between"
            @click="
              ids.idApartado = item.idApartado;
              ids.idActividad = '';
              actualizarArrays();
            "
          >
            <h1>
              <span v-if="item.select" :class="[item.select ? 'mr-1' : '']">
                👉
              </span>
              <span class="font-bold">{{ index + 1 }}.</span>
              {{ item.apartado }}
            </h1>
            <div class="flex flex-row items-center">
              <button
                class="text-red-400 px-1 py-1 mr-1"
                @click="eliminarApartado(item.idApartado)"
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="24"
                  height="24"
                  viewBox="0 0 24 24"
                  fill="none"
                  stroke="currentColor"
                  stroke-width="2"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                >
                  <polyline points="3 6 5 6 21 6"></polyline>
                  <path
                    d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"
                  ></path>
                  <line x1="10" y1="11" x2="10" y2="17"></line>
                  <line x1="14" y1="11" x2="14" y2="17"></line>
                </svg>
              </button>
            </div>
          </div>
        </div>
      </div>

      <!-- COLUMNA 3 -->
      <div class="w-1/3 flex flex-col items-center justify-start h-full px-2">
        <div class="py-2 flex flex-row items-center justify-start w-full">
          <div class="rounded-lg shadow">
            <input
              type="text"
              name=""
              class="bg-white py-3 px-3 rounded-l-lg focus:outline-none"
              placeholder="Añadir actividad"
              v-model="titulos.actividad"
            />
            <button
              class="
                bg-blue-300
                text-blue-500
                p-3
                rounded-r-lg
                focus:outline-none
              "
              @click="crearActividad"
            >
              Añadir
            </button>
          </div>
        </div>
        <div
          class="
            py-2
            flex flex-col
            w-full
            items-center
            justify-start
            max-h-90
            overflow-y-auto
          "
        >
          <div
            v-for="(item, index) in arrayActividades"
            :key="index"
            class="actividad flex items-center justify-between"
            @click="
              ids.idActividad = item.idActividad;
              actualizarArrays();
            "
          >
            <h1 class="flex-none">
              <span v-if="item.select" :class="[item.select ? 'mr-1' : '']"
                >👉
              </span>
              <span class="font-bold">{{ index + 1 }}.</span>
              {{ item.actividad }}
            </h1>
            <div class="flex flex-row items-center">
              <button
                class="text-red-400 px-1 py-1 mr-1"
                @click="eliminarActividad(item.idActividad)"
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="24"
                  height="24"
                  viewBox="0 0 24 24"
                  fill="none"
                  stroke="currentColor"
                  stroke-width="2"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                >
                  <polyline points="3 6 5 6 21 6"></polyline>
                  <path
                    d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"
                  ></path>
                  <line x1="10" y1="11" x2="10" y2="17"></line>
                  <line x1="14" y1="11" x2="14" y2="17"></line>
                </svg>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import uniqid from "uniqid";
import { computed, ref } from "@vue/reactivity";
import { useStore } from "vuex";
import { onMounted } from "@vue/runtime-core";

export default {
  setup() {
    const store = useStore();

    const titulos = ref({
      sabana: "",
      apartado: "",
      actividad: "",
    });

    const ids = ref({
      idHotel: 0,
      idSabana: "",
      idApartado: "",
      idActividad: "",
    });

    const arraySabanas = ref([]);
    const arrayApartados = ref([]);
    const arrayActividades = ref([]);

    onMounted(() => {
      sabana();
    });

    const sabana = async () => {
      await store.dispatch("getArray", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "sabana",
      });
      await actualizarArrays();
    };

    const array = computed(() => {
      actualizarArrays(store.state.array);
      return store.state.array;
    });

    const hoteles = computed(() => {
      return store.state.hoteles;
    });

    const crearSabana = async () => {
      if (!titulos.value.sabana.length) return;
      if (ids.value.idHotel == 0) return;

      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "crearSabana",
        idHotel: ids.value.idHotel,
        idSabana: uniqid() + uniqid(),
        sabana: titulos.value.sabana,
      });

      if (response.response) {
        console.log(response.data.data);
        titulos.value.sabana = "";
        sabana();
      }
      if (!response.response) console.log("NO Creado");
    };

    const crearApartado = async () => {
      if (!titulos.value.apartado.length) return;
      if (!ids.value.idSabana.length) return;

      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "crearApartado",
        idSabana: ids.value.idSabana,
        idApartado: uniqid() + uniqid(),
        apartado: titulos.value.apartado,
      });

      if (response.response) {
        titulos.value.apartado = "";
        sabana();
      }
      if (!response.response) console.log("NO Creado");
    };

    const crearActividad = async () => {
      if (!titulos.value.actividad.length) return;
      if (!ids.value.idApartado.length) return;

      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "crearActividad",
        idSabana: ids.value.idSabana,
        idApartado: ids.value.idApartado,
        idActividad: uniqid() + uniqid(),
        actividad: titulos.value.actividad,
      });

      if (response.response) {
        titulos.value.actividad = "";
        sabana();
      }
      if (!response.response) console.log("NO Creado");
    };

    const actualizarArrays = () => {
      arraySabanas.value = [];
      arrayApartados.value = [];
      arrayActividades.value = [];

      store.state.array.forEach((sabanas, index1) => {
        arraySabanas.value.push(sabanas);
        arraySabanas.value[index1].select = false;

        if (sabanas.idSabana === ids.value.idSabana) {
          arraySabanas.value[index1].select = true;
          sabanas.apartados.forEach((apartados, index2) => {
            arrayApartados.value.push(apartados);
            arrayApartados.value[index2].select = false;
            if (apartados.idApartado === ids.value.idApartado) {
              arrayApartados.value[index2].select = true;
              apartados.actividades.forEach((actividades, index3) => {
                arrayActividades.value.push(actividades);
                arrayActividades.value[index3].select = false;
                if (actividades.idActividad === ids.value.idActividad) {
                  arrayActividades.value[index3].select = true;
                }
              });
            }
          });
        }
      });
    };

    const eliminarApartado = async (idApartado) => {
      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "eliminarApartado",
        idApartado: idApartado,
      });

      if (response.response) {
        sabana();
      }
      if (!response.response) console.log("NO Creado");
    };

    const eliminarActividad = async (idActividad) => {
      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "eliminarActividad",
        idActividad: idActividad,
      });

      if (response.response) {
        sabana();
      }
      if (!response.response) console.log("NO Creado");
    };

    return {
      array,
      hoteles,
      crearSabana,
      crearApartado,
      crearActividad,
      titulos,
      ids,
      arraySabanas,
      arrayApartados,
      arrayActividades,
      actualizarArrays,
      eliminarActividad,
      eliminarApartado,
    };
  },
};
</script>

<style scoped>

.max-h-90 {
  max-height: 70%;
}

.sabana {
  @apply w-full text-left px-4 bg-white py-4 cursor-pointer rounded-lg hover:shadow hover:bg-blue-50 mb-1;
}
.sabana:active {
  @apply bg-bluedemon-500 text-white my-2;
}

.actividad {
  @apply w-full text-left px-2 bg-white py-2 cursor-pointer hover:bg-blue-50 mb-1;
}
</style>