import { createStore } from 'vuex'

export default createStore({
  state: {
    array: [],
    hoteles: []
  },

  mutations: {
    setArray(state, payload) {
      console.log(payload);

      state.array = payload.data;
      state.hoteles = payload.hoteles;
    }
  },

  actions: {
    // OBTIENE DATOS DEL ARRAY
    async getArray({ commit }, array) {

      try {
        const res = await fetch(`../apis/sabanas.php`, {
          method: "POST",
          body: JSON.stringify(array),
        });
        const data = await res.json();
        if (data.response == "SUCCESS") {
          commit("setArray", data);
          return { response: true, data: data };
        }

        return { response: false, data: [] };
      } catch (error) {
        console.log(error);
        return { response: false, data: [] };
      }
    },

    // OBTIENE DATOS DEL ARRAY
    async accionesUsuario({ commit }, array) {
      try {
        const res = await fetch(`../apis/sabanas.php`, {
          method: "POST",
          body: JSON.stringify(array),
        });
        const data = await res.json();
        console.log(data);
        if (data.response == "SUCCESS") {
          return { response: true, data: data };
        }

        return { response: false, data: [] };
      } catch (error) {
        console.log(error);
        return { response: false, data: [] };
      }
    },




  },
  modules: {
  }
})
