import { createRouter, createWebHashHistory } from 'vue-router'
import Sabanas from '../views/Sabanas.vue'

const routes = [
  {
    path: '/',
    name: 'Sabanas',
    component: Sabanas
  },
  {
    path: '/crear',
    name: 'Nuevasabana',
    component: () => import(/* webpackChunkName: "about" */ '../views/Nuevasabana.vue')
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
